<template>
  <div class="agreement-view">
    <header-view :title="title" :fixed="fixed" :routerName="routerName"></header-view>
    <article class="notice-context" v-html="protocol.content"></article>
  </div>
</template>

<script>
  import {getProtocol} from '../../service/getData'
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        title: '和享时代用户协议',
        routerName: 'register',
        fixed: true,
        protocol: ''
      }
    },
    created: function () {
      var _this = this
      getProtocol.then(res => {
        _this.protocol = res.data
      }).catch(() => {
        _this.$toast('用户协议请求失败！')
      })
    }

  }
</script>

<style>
  /* 公告内容页 */
  .agreement-view{
    padding-top: .4rem;
    height: 100%;
  }
  
  .notice-context {
    height: 100%;
    padding: .1rem;
    overflow: auto;
    text-indent: .28rem;
    -webkit-overflow-scrolling: touch;
  }
  
  .notice-context p {
    padding-bottom: .3rem;
  }
</style>


